.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
/****************** Common Classes *************************/
.overflow {
  overflow-x: hidden;
}
.mb-20 {
  margin-bottom: 20px;
}
.mt-20 {
  margin-top: 20px;
}
.mr-15 {
  margin-right: 15px;
}
.pd-15 {
  padding: 15px 0px;
}
.pr-15 {
  padding-right: 15px;
}
.pl-0 {
  padding-left: 0px;
}
.font-bold {
  font-weight: 700;
}

/****************** END Common Classes *************************/
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
